.axew-toast-container {
  position: fixed;
  z-index: 99999;
}
.axew-toast-content {
  position: fixed;
  top: 53%;
  left: 0;
  right: 0;
  text-align: center;
  -webkit-animation: axewToastFadeInUp 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) 1 normal forwards;
          animation: axewToastFadeInUp 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) 1 normal forwards;
  pointer-events: none;
  touch-action: none;
}
.axew-toast-wrap {
  font-size: 0;
  background-color: rgba(58, 58, 58, 0.9);
  display: inline-block;
  border-radius: 6px;
  max-width: 80%;
  pointer-events: auto;
  touch-action: auto;
}
.axew-toast-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
.axew-toast-text {
  font-size: 14px;
  line-height: 20px;
  word-break: break-all;
  white-space: pre-line;
  color: #fff;
  padding: 12px;
  margin: 0;
}
.axew-toast-hide {
  -webkit-animation-name: axewToastFadeOutDown;
          animation-name: axewToastFadeOutDown;
}
.axew-toast-loading {
  width: 36px;
  height: 36px;
  display: inline-block;
  -webkit-animation: axewToastLoading 1s steps(12, end) infinite;
          animation: axewToastLoading 1s steps(12, end) infinite;
  background: transparent url(data:image/svg+xml;base64,) no-repeat;
  background-size: 100%;
  margin: 6px;
}
@-webkit-keyframes axewToastFadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
@keyframes axewToastFadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
@-webkit-keyframes axewToastFadeOutDown {
  0% {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
}
@keyframes axewToastFadeOutDown {
  0% {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
}
@-webkit-keyframes axewToastLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }
  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}
@keyframes axewToastLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }
  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}
